form{margin-bottom:5vh;display:flex;justify-content:center;align-items:center;width:100%}form input,form button{padding:.5rem;font-size:2rem;border:none;background:#fff}form button{color:#ff6f47;background:#f7fffe;cursor:pointer;transition:all .3s ease;margin-right:1rem}form button:hover{background:#ff6f47;color:#fff}form .watch-input{outline:none;width:100%}form .select{margin:1rem 0;position:relative;overflow:hidden}form .select::after{content:"▼";position:absolute;top:0;right:0;padding:1rem;background:#ff6f47;cursor:pointer;pointer-events:none}form .select .filter-watch{padding:1rem;-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;outline:0;box-shadow:none;border:0 !important;background-image:none;color:#0bd4a2;font-family:"Poppins",sans-serif;cursor:pointer;width:14rem}